export default {
  name: "recommand_javs",
  data() {
    return {
      recommend_javs: undefined
    }
  },
  destroyed() {
    this.listener_init.off();
  },
  created() {
    this.init();
    this.listener_init = this.$hub.on('event:init', () => {
      this.init();
      this.$forceUpdate();
    });
  },
  methods: {
    init: function () {
      this.update_threads();

    },
    update_threads: function () {
      this.recommend_javs = this.get_recommend_javs();
      console.log('recommend_javs:', this.recommend_javs);
    },
    get_recommend_javs: function () {
      try {
        if (this.$store.state.init.data.recommend_javs.datas.length > 0) {
          var recommend_javs_datas = this.$store.state.init.data.recommend_javs.datas;
          if (recommend_javs_datas.length > 8) {
            recommend_javs_datas = recommend_javs_datas.slice(0, 8)
          }
          return recommend_javs_datas;
        }
      } catch (e) {

      }
      return undefined
    }
  }
}
